<template>
   <div class="media m-2">
      <img class="mr-3 rounded"
           :src="imgsrc"
           alt="Generic placeholder image">
      <div class="media-body">
         <h5 class="mt-0">{{title}}</h5>
         {{text}}
      </div>
   </div>
</template>

<script>
export default {
  name: "Media",
  props: {
    text: String,
    title: String,
    imgsrc: String
  }
};
</script>



